export default class TodoModel{
    constructor(){
        //localStoragekey
        this.STORE_KEY = 'todos'
        //todo
        this.todos = localStorage.getItem(this.STORE_KEY)?JSON.parse(localStorage.getItem(this.STORE_KEY)):[];
        //
        this.listeners = []
    }
    subscribe(listener){
        //react render
        this.listeners.push(listener)
    }
    emit(){
        this.listeners.forEach(l=>l());
    }
    //
    setTodos = (todos) =>{
        this.todos = todos;
        localStorage.setItem(this.STORE_KEY,JSON.stringify(this.todos))
        this.emit()
    }
    //
    addTodo = (todo)=>{
        this.todos=[...this.todos,todo];
        this.setTodos(this.todos)
    }
    //
    toggle = (id) => {
        let todos=this.todos.concat();
        todos.forEach(todo=>{
            if(todo.id === id){
                todo.completed=!todo.completed
            }
        });
        this.setTodos(todos);
    }
    //
    removeTodo = (id)=>{
        let todos=this.todos.concat();
        let index = todos.findIndex(todo=>todo.id === id);
        todos.splice(index,1);
        this.setTodos(todos)
    }
    //
    toggleAll = (count)=>{
        let todos=this.todos.concat();
        if(count===0){
            todos.forEach((todo)=>{
                todo.completed=false;
            })
        }else{
            todos.forEach((todo)=>{
                todo.completed=true;
            })
        }
        this.setTodos(todos)
    }
    //
    clearCompleted = ()=>{
        let todos = this.todos;
        todos=todos.filter(todo=>!todo.completed)
        this.setTodos(todos)
    }
}


