import React from 'react'
import TodoHeader from './TodoHeader'
import TodoFooter from './TodoFooter'
import TodoItem from './TodoItem'

//css
import 'zui/dist/css/zui.min.css'
import 'zui/dist/css/zui-theme.min.css'
import 'zui/dist/css/zui.lite.min.css'

import * as filterTypes from './filterTypes'

export default class TodoApp extends React.Component{
    constructor(props){
        super(props);
        this.state={
            filterType:filterTypes.ALL
        }
    }
    
    changeFilterType = (filterType)=>{
        this.setState({filterType})
    };
    
    render(){
        let todos = this.props.model.todos;
        let activeTodoCount = todos.reduce((count,todo)=>count+(todo.completed?0:1),0);
        let completedTodoCont = todos.length-activeTodoCount
        let showTodos = todos.filter((todo)=>{
            switch(this.state.filterType){
                case filterTypes.ACTIVE:
                    return !todo.completed;
                case filterTypes.COMPLETED:
                    return todo.completed;
                default:
                    return true;
            }
        });
        let selectAll = showTodos.length>0?'block':'none';
        let main = (
            <ul className="list-group">
                <li className="list-group-item" style={{display:selectAll}}>
                    <div className="row">
                        <div className="col-xs-1">
                            <input onChange={()=>{
                                this.props.model.toggleAll(activeTodoCount)
                            }} type="checkbox" checked={activeTodoCount === 0}/>
                        </div>
                        <div className="col-xs-11">
                            {
                                activeTodoCount===0?'':''
                            }
                        </div>
                    </div>
                </li>
                {
                    showTodos.map((todo,index)=>{
                        return (
                            <TodoItem removeTodo={this.props.model.removeTodo} toggle={this.props.model.toggle} key={index} todo={todo} index={index}/>
                        )
                    })
                }

            </ul>
        );
        return (
            <div className="container" style={{marginTop:'20px'}}>
                <div className="row">
                    <div className="col-md-6 col-md-offset-3">
                        <div className="panel panel-default todo-wrapper">
                            <div className="panel-heading">
                                <TodoHeader addTodo={this.props.model.addTodo}/>
                            </div>
                            <div className="panel-body">
                                {main}
                            </div>
                            <div className="panel-footer">
                                <TodoFooter completedTodoCont={completedTodoCont} clearCompleted={this.props.model.clearCompleted} type={this.state.filterType} changeFilterType={this.changeFilterType} activeTodoCount={activeTodoCount}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


