import React from 'react'
export default class TodoItem extends React.Component{
    render(){
        let todo = this.props.todo;
        return (
            <li className="list-group-item">
                <div className="row">
                    <div className="col-xs-1">
                        <input type="checkbox" onChange={()=>{this.props.toggle(todo.id)}} checked={todo.completed}/>
                    </div>
                    <div className="col-xs-10" style={{textDecoration:todo.completed?'line-through':''}}>
                        {todo.title}
                    </div>
                    <div className="col-xs-1">
                        <button onClick={()=>{this.props.removeTodo(todo.id)}} className="btn btn-danger btn-xs pull-right">X</button>
                    </div>
                </div>
            </li>
        )
    }
}


