import React from 'react'
import * as filterTypes from './filterTypes'
export default class TodoFooter extends React.Component{
    render(){
        let clearbtnStyle = this.props.completedTodoCont>0?'block':'none';
        return (
            <div className="row">
                <div className="col-xs-3">
                    {
                        this.props.activeTodoCount>0?
                        <button className="btn btn-info"> <span className="label label-badge">{this.props.activeTodoCount}</span></button>
                        :
                        null
                    }
                </div>
                <div className="col-xs-6 text-center">
                    <button onClick={()=>{this.props.changeFilterType(filterTypes.ALL)}} className={`btn ${this.props.type===filterTypes.ALL?'btn-success':''}`}></button>
                    <button onClick={()=>{this.props.changeFilterType(filterTypes.ACTIVE)}} className={`btn ${this.props.type===filterTypes.ACTIVE?'btn-success':''}`} style={{marginLeft:'10px'}}></button>
                    <button onClick={()=>{this.props.changeFilterType(filterTypes.COMPLETED)}} className={`btn ${this.props.type===filterTypes.COMPLETED?'btn-success':''}`} style={{marginLeft:'10px'}}></button>
                </div>
                <div className="col-xs-3">
                    <button style={{display:clearbtnStyle}} onClick={this.props.clearCompleted} className="btn btn-danger"></button>
                </div>
            </div>
        )
    }
}


