import React from 'react'
const ENTER_KEY = 13;
export default class TodoHeader extends React.Component{
    handleKeyDown = (event)=>{
        let title=this.title.value;
        if(event.keyCode === ENTER_KEY){
            if(title===''){
                alert('');
                event.target.focus();
            }else{
                this.props.addTodo({id:Date.now(),title,completed:false})
                event.target.value="";
            }
        }
    };
    handleSubmit = (event)=>{
        event.preventDefault();
    };
    render(){
        return (
            <form onSubmit={this.handleSubmit}>
                <div className="form-group">
                    <div className="input-control">
                        <input ref={x=>this.title=x} onKeyDown={this.handleKeyDown} autoFocus={true} type="text" className="form-control" placeholder=""/>
                    </div>
                </div>
            </form>
        )
    }
}


